.container {
    padding: 15px 20px 20px 20px;
    width: 100%;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
}

.header {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    text-transform: uppercase;
    font-size: 500;
    color: rgba(171, 177, 197, 1);
    margin-bottom: 16px;
}

.imageBox {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(242, 244, 247, 1);
    border-radius: 10px;
    width: 100%;
    padding: 27px 0 24px 0;
    margin-bottom: 15px;
}

.image {
    width: 200px;
    height: 105px;
}

.description {
    font-size: 16px;
    line-height: 22px;
    color: rgba(20, 35, 61, 1);
    font-weight: 500;
    margin-bottom: 10px;
}

.price {
    font-size: 18px;
    color: rgba(41, 127, 255, 1);
    margin-bottom: 15px;
    font-weight: 700;
}

.description--booked, .price--booked {
    color: rgba(171, 177, 197, 1);
}

.bottom {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.notification {
    padding: 10px;
    border: 1px solid rgba(221, 227, 235, 1);
    border-radius: 10px;
    color: rgba(171, 177, 197, 1);
    font-size:16px;
    font-weight: 600;
}

.freeDates {
    color: rgba(41, 127, 255, 1);
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    max-width: 126px;
}